div.menu > div.category {
    position: relative;
    display: inline-block;
    vertical-align: top;
    margin: 1em;
    box-shadow: .2em .2em 1px rgba(100,80,20,.5);
    border-radius: .5em;
    padding: 1em;
    background: linear-gradient(#111,#777);
    color: #ffe;
    border: 2px solid #000;
}

div.menu > div.category::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 80%;
    border-radius: 0 0 100% 0;
    background: rgba(255,255,255,.2);
    pointer-events: none;    
}


div.menu a,
div.menu a:visited {
    color: #3e0;
    text-decoration: none;
    font-family: sans-serif;
}

div.menu a:hover {
    text-decoration: underline;            
}

div.menu > div.category > div.category-name {
    font-weight: 200;
    font-size: 1.8rem;
    font-variant: small-caps;
    text-shadow: 2px 2px #000;
    font-family: sans-serif;
}

div.menu > div.category > div.articles {
    display: table;
}

div.menu > div.category > div.articles > div {
    display: table-row;
}

div.menu > div.category > div.articles > div > div {
    display: table-cell;
    vertical-align: middle;
}

div.menu > div.category > div.articles > div > div:last-child {
    text-align: right;
    padding-left: 1em;
}


